.output__container {
  background: #1d1a1c;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.output__contianer--inner {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #ffffff;
  border-radius: 8px;
  height: 60%;
  width: 35%;
}

@media (min-width: 320px) and (max-width: 480px) {
  .output__container {
    height: 100%;
  }

  .output__contianer--inner {
    width: 100%;
    height: 100%;
  }
}

@media (min-width: 480px) and (max-width: 1200px) {
  .output__contianer--inner {
    width: 60%;
  }
}
