.info-bar {
  background: #570046;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 4px 4px 0 0;
  height: 60px;
  width: 100%;
  font-family: "Lobster Two", cursive;
}

.info-bar__left {
  display: flex;
  flex: 0.5;
  align-items: center;
  margin-left: 5%;
  color: white;
}

.info-bar__right {
  display: flex;
  flex: 0.5;
  justify-content: flex-end;
  margin-right: 5%;
}

.online-icon {
  margin-right: 5%;
}
