.message__container {
  display: flex;
}

.justify-end {
  justify-content: flex-end;
  align-items: center;
}

.justify-start {
  justify-content: flex-start;
  align-items: center;
}

.justify-end .message__box {
  background-color: #d400ab;
  color: #e9e9e9;
  padding: 0 0.5rem;
  border-radius: 10px;
  margin: 1.2rem;
}

.message__send {
  color: #80707c;
}

.justify-start .message__box {
  background-color: #1a0015;
  color: #e9e9e9;
  padding: 0 0.5rem;
  border-radius: 10px;
  margin: 1.2rem;
}
