.join__container {
  display: grid;
  align-items: center;
  justify-items: center;
  height: 100vh;
  background: #1d1a1c;
  font-family: "Lobster Two", cursive;
}

.join__container--inner {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 75%;
  margin-top: 8rem;
  width: 100%;
}

.join__form-container {
  width: 25%;
  display: flex;
  flex-direction: column;
  min-width: 12rem;
}

.join__input {
  padding: 0.5rem;
  font-size: 0.7rem;
}

.join__button {
  width: 100%;
  padding: 0.7rem;
  border-radius: 10px;
  border: none;
  background: rgb(110, 154, 250);
  color: #e9e9e9;
}

.join__input:focus,
.join__button:focus {
  outline: transparent;
}

.join__button:hover,
.join__button:active {
  background: #e9e9e9;
  color: #d400ab;
}

.join__heading {
  color: #d400ab;
  width: 25%;
  min-width: 12rem;
  text-align: center;
  font-size: 2.5rem;
  border-bottom: 1.5px solid seashell;
  padding-bottom: 0.8rem;
}
