.form {
  display: flex;
  border-top: 2px solid #d3d3d3;
}

.form__input {
  border: none;
  border-radius: 0;
  padding: 5%;
  width: 80%;
  font-size: 1.2rem;
}

.form__input:focus,
textarea:focus,
select:focus {
  outline: none;
}

.form__button {
  color: #e9e9e9;
  background: #570046;
  text-transform: uppercase;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
  border: none;
  width: 20%;
}
